import React from 'react';
import './Items.css';
import Item from '../Item/Item';








export default function Items(props) {
    return (
        <div className="items-container">
     {(props.items).map(item=>{
       return(
          <Item image={item.image} price={item.price} name={item.name}/>
        )
    })} 
        </div>
    )
}
