import React,{useState} from 'react';
import './App.css';
import Items from './components/Items/Items';

import vr from './images/vr.png';
import joystick from './images/joystick.png';
import keyboard from './images/keyboard.png';
import display from './images/display.png';
import mouse from './images/mouse.png';
import gaminggun from './images/gaminggun.png';
import gamingcar from './images/gamingcar.png';
import gamingpc from './images/gamingpc.png';


const itemsList=[
  {
      name:"Vr",
      price:"$2000",
      image:vr
  },
  {
      name:"Joy-stick",
      price:"$50",
      image:joystick
  },
  {
      name:"Key-board",
      price:"$100",
      image:keyboard
  },
  {
      name:"Display",
      price:"$500",
      image:display
  },
  {
      name:"Mouse",
      price:"$50",
      image:mouse
  },
  {
      name:"Gaming-gun",
      price:"$150",
      image:gaminggun
  },
  {
      name:"Gaming-car",
      price:"$500",
      image:gamingcar
  },
  {
      name:"Gaming-pc",
      price:"$1500",
      image:gamingpc
  }
]



function App() {

const [searchValue,setSearchValue]=useState("");
const [items,setItems]=useState(itemsList)

const searchItem=(e)=>{
  setSearchValue(e.target.value);

 setItems(itemsList.filter(item=>{return ((item.name).toLocaleLowerCase()).includes((searchValue).toLocaleLowerCase())}))
  if(searchValue==""){
    setItems(itemsList);
  }
}


 return (
    <div className="App">

        <Items items={items}/>
      <div className="search-bar">
        <input type="text" value={searchValue} onChange={searchItem} className="search-input" placeholder="search"/>
      </div>  
    </div>
  )
}

export default App;
